.header {
    padding: 25px;
    max-width: 970px;
    width: 100%;
    margin: 0 auto;
    font-family: neue-haas-grotesk-display;
    box-sizing: border-box;

    h1 {
        text-align: center;
        padding-bottom: 2.1875rem;
        padding-top: 2.5rem;
        letter-spacing: .063rem;
        font-size: 3rem;
        line-height: 3rem;

        @media(min-width: 920px) {
            font-size: 5.0625rem;
            line-height: 1.875rem;
        }
    }

    h2 {
        color: rgb(130, 137, 161);
        margin-top: 0;
        margin-bottom: 0;
        padding-top: 0;
    }

    h3 {
        font-size: 1.6875rem;
        line-height: 1.875rem;
        letter-spacing: 0.03375rem;
    }

    .columns.noVideo
    {
        @media(min-width: 821px) {            

            gap: 2.5%;
            display: block;
            grid-template-columns: 3fr 4fr;
            vertical-align: top;
            margin-bottom: 3rem;
        }
    }

    .columns {
        
        >div:last-child {
            display: none;
        }

        >div:last-child:first-child {
            display: block;
        }

        @media(min-width: 821px) {            

            gap: 2.5%;
            display: grid;
            grid-template-columns: 3fr 4fr;
            vertical-align: top;
            margin-bottom: 3rem;

            >div:last-child {
                border-left: 1px solid black;
                display: block;
                padding-left: 5%;
            }

            >div:last-child:first-child{
                border-left: none;
                max-width: 400px;
                margin: 0 auto;
            }

        }

        div.summary {
            padding-right: 5%;
            display: grid;
            padding-bottom: 5%;
            border-bottom: 1px solid black;
            grid-template-columns: 1fr 3fr 2fr;

            img {
                width: 100%;
                height: auto;
            }

            >div:first-child {
                font-size: 5.4375rem;
                font-weight: 900;
                margin-top: -1.25rem;
                text-align: center;
                margin-bottom: 1.25rem;
            }

        }
    }
}

.navigation {
    justify-content: space-between;
    border-top: 6px solid;
    border-bottom: .188rem solid;
    padding-top: 1rem;
    padding-bottom: 1rem;
    display: flex;
    margin-bottom: 56px;

    >a {
        color: rgb(26, 72, 196);
        font-size: 0.9375rem;
        line-height: 1.5625rem;
        font-weight: 900;
        text-transform: uppercase;
        text-decoration: none;

    }

    .navigation>a:global(".arrow-right") {
        justify-content: flex-end;
    }
}


.table {

    background-color: rgb(237, 235, 235);
    width: 100%;
    font-family: neue-haas-grotesk-display;
    padding: 1rem 15px;
    box-sizing: border-box;

     .dropOuts{
        gap: 0 15px;
        > div {
            
            text-align: left; margin-bottom: 15px; background-color: white; border: 1px solid black; 
            border-radius: 5px;
            display: inline-grid; width: 100%; grid-auto-columns: max-content; gap: 0 15px;
            grid-auto-flow: column;
            > img { display: block;}
            position: relative;
            > div {
                margin-right: 15px;
                align-self: center;
                > h2 { margin-bottom: 5px; font-size: 1.025rem}
                > p{ margin-top: 0; font-size: 0.8rem; }
                /*> h2, > p { white-space: nowrap; overflow: hidden; text-overflow: ellipsis;  }*/
            } 
        }
        margin: 0 auto;
        @media(min-width: 821px) {
            display: grid; grid-template-columns: 1fr 1fr;
            width: 970px;
            > div > div > h2 { font-size: 1.125rem;}
            > div > div > p { font-size: 0.9375rem;}
        }
    }


    >h2 {
        display: block;
        padding-top: 1.5rem;
        padding-bottom: 1rem;
        max-width: 970px;
        margin: 15px auto;
        font-size: 1.875rem;
        line-height: 1.875rem;

        >span {
            float: none;
            display: block;
            text-transform: uppercase;
            font-weight: normal;
            font-size: 1rem;
            line-height: 1.875rem;

            @media(min-width: 821px) {
                display: inline;
                float: right;
            }
        }
    }

    .row {
        >div {
            display: none;

            >img {
                width: 100%;
                height: auto;
                display: block;
            }

            border-bottom: 1px solid black;
        }

        >div:nth-child(1),
        >div:nth-child(2),
        >div:nth-child(3),
        >div:nth-child(4) {
            display: table-cell;
        }

        >div:nth-child(3) {
            width: 14%;
            text-align: center;
        }

        >div.thin:nth-child(1) {
            width: 10%;
            @media(min-width: 821px)
            {
                font-size: 2rem;
            }
        }

        >div:nth-child(2) {
            width: 16%;
        }

        .row>div:nth-child(4) {
            width: 60%;
        }

        >div:first-child {
            font-size: 1.5rem;
            font-weight: 900;
            background-color: black;
            color: white;
            text-align: center;
            border-bottom: 1px solid white;
        }

        @media(min-width: 821px) {
            >div:first-child {
                font-size: 2rem;
            }
        }


        >div:nth-child(5),
        >div:nth-child(7) {
            background-color: rgb(237, 235, 235);
            transition: background-color 0.5s;
        }



        >div:nth-child(3) {
            padding-left: 0rem;
            padding-right: 0rem;
        }

        >div:first-child {
            font-size: 1.5rem;
            font-weight: 900;
            background-color: black;
            color: white;
            text-align: center;
            border-bottom: 1px solid white;
        }

    }


    .row:nth-child(3)>div:nth-child(2) {
        width: 25%;
    }

    .row:nth-child(3)>div:nth-child(4) {
        width: 51%;
    }

    .row:nth-child(3)>div:nth-child(6),
    .row:nth-child(3)>div:nth-child(7),
    .row:nth-child(3)>div:nth-child(8) {
        font-size: 2rem;
        font-weight: 900;
    }

    .row:nth-child(3)>div h3 {
        font-size: 1.2rem;
        @media(min-width: 821px) {
            font-size: 1.4375rem;
        }
    }

    .row:nth-child(3)>div span.artist {
        font-size: 1rem;
        max-width: calc(100% - 40px);
        @media(min-width: 821px) {
            font-size: 1.25rem;
        }
    }

    @media(min-width: 821px) {

        .row>div:nth-child(2) {
            width: 11%;
        }
        .row>div:nth-child(3) {
            width: 9%;
        }

        .row:nth-child(3)>div:nth-child(2) {
            width: 21%;
        }

        .row>div:nth-child(4) {
            width: 40%;
        }

        .row:nth-child(3)>div:nth-child(4) {
            width: 30%;
        }
    }



    .panel
    {

        h2 { text-transform: uppercase;}

        background-color: white;
        margin: 0 auto;
        width: 100%;
        border-bottom: 1px solid black;
        padding: 25px 10px;

        .history 
        {
            margin-top: 15px; margin-bottom: 25px;
            display: flex; flex-wrap: wrap; gap: 10px; grid-auto-columns: min-content; grid-auto-flow: column;
            > a, > div {      font-family: neue-haas-grotesk-display;
                border: 1px solid grey; padding: 15px; text-align: center; min-width: 50px; display: block; text-decoration: none; color: black;}
            > .isPeak { border-color: #005581; border-width: 3px; padding: 13px;}
            > .currentWeek { background-color:#D5045a; color: white;}
        } 

        @media(min-width: 821px) {
            max-width: 970px;
            margin: 0 auto;
            padding: 25px 25px;

        }

        box-sizing: border-box;
        border-left: 1px solid grey;
        border-right: 1px solid grey;

        .data{     
            display: grid; grid-template-columns: 1fr 1fr;  
            padding: 15px;
            border-bottom: 1px dashed grey;
            > div:last-child { text-align: right; font-weight: bold;}
        }
        
        .data:last-child { border-bottom: none;}
    }


    .row > div:nth-child(4)
    {
        border-right: 1px solid grey;
        position: relative;
        .expandIcon {
            min-width: 25px;
            text-align: center;
            font-weight:600;
            position: absolute; right: 15px; top: 50%; transform: translateY(-50%); cursor: pointer; color: grey;
            font-size: 2.5em;
        }
    }

    @media(min-width: 821px) {

        .row > div:nth-child(4)
        {
            border-right: none;
        }
        .row > div:last-child { 
        border-right: 1px solid grey;
    }
    }

    .row.noBorder > div
    {
        border-bottom: none;
        background-color: white;
    }

    .row.noBorder > div:first-child
    {
        background-color: black;
    }

    .row.noBorder {
        > div > span.expandIcon { color: #005581;}
    }
    .row.noBorder {
        > div:nth-child(5)
        {
            color: #005581;
        }
        > div:nth-child(6), > div:nth-child(7), > div:nth-child(8)
        {
            font-weight: bold; color: #005581;
        }
    }

    .row.highlightRow {
        transition: all 0.5s;
        background-color: #005581 !important;
        > div:nth-child(5), > div:nth-child(7) {        transition: background-color 0.5s;
            background-color: #005581; }
        color: white;
        > div > span.expandIcon { color: white; }
    }

    .row {
        transition: all 0.5s;
    }

    .row,
    .headerRow {
        background-color: white;
        margin: 0 auto;
        display: table;
        table-layout: fixed;
        width: 100%;

        @media(min-width: 821px) {
            max-width: 970px;
            margin: 0 auto;
        }
    

        >div {
            vertical-align: middle;
        }

        @media(min-width: 821px) {
            >div {
                display: table-cell;
            }

        }

    }
    
    .row.lastRow {
        > div:first-child { border-bottom-left-radius: 5px;}
        > div:last-child { border-bottom-right-radius: 5px;}
    }

    .headerRow {

        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
        background-color: rgb(26, 72, 196);
        color: white;

        >.thinnest {
            display: none;
        }

        >div:nth-child(1) {
            display: table-cell;
            width: 100%;
            text-align: center;
        }

        >div:first-child {
            text-align: center;
        }

        background-color: rgb(26, 72, 196);
        color: white;

        >div {
            font-size: 0.71875rem;
            font-weight: bold;
            line-height: 1.125rem;
            text-transform: uppercase;
            padding-top: 0.75rem;
            padding-bottom: .75rem;
        }

        @media(min-width: 821px)
        {

            //> div { font-size: 1.0625rem;}
            > div.thinnest {
            display: table-cell;}
            >div:nth-child(1) {
                width: 66%; text-align: left;
                padding-left: 1rem;
            }
            >div:nth-child(2) { display: none;}
        }
    }

}

h3 {
    font-size: 1.025rem;
    @media(min-width: 821px)
    {
        font-size: 1.125rem;
    }
    margin-top: 0;
    margin-bottom: 0;
    padding-top: 0;
    padding-bottom: 0;
}

.newThisWeek {
    padding: 2.5rem 0;
    text-align: center;
    color: rgb(26, 72, 196);
    font-size: 1.25rem;
    line-height: 1.5rem;
    letter-spacing: 0.0375rem;
    font-weight: 900;
}

.thin {
    width: 10%;
}

.thinnest {
    width: 7.5%;
    text-align: center;
}

.moreData {
    padding-top: 2.5rem;
    padding-bottom: 1rem;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 5px;

    >div {
        >p {
            text-transform: uppercase;
            font-size: 0.6875rem;
            text-align: center;
            line-height: 1.125rem;
            font-weight: 900;
        }

        h3 {
            font-size: 2.1875rem;
            line-height: 2.5625rem;
            letter-spacing: 0.05rem;
            text-align: center;
            font-weight: 900;
        }
    }
}



.new {

    padding: 0.1rem 0.2rem;
    font-size: 0.6rem;
    background-color: #fff100;
    color: rgb(43, 46, 60);
    letter-spacing: 0.05125rem;
    line-height: 1.125rem;
    font-weight: 900;    

    @media(min-width: 821px)
    {
        padding: 0.1rem 0.4rem;
        font-size: 0.6875rem;
    }
}

.rentry {
    display: inline-block;
    margin: 0 auto;
    line-height: 1.2;
    letter-spacing: 0;
}

.loser,
.riser,
.same,
.award {
    width: 1.625rem;
    height: 1.625rem;
    display: inline-block;
}

.award {
    width: 1.4rem;
    height: 1.4rem;
}

.topentry {
    width: 2.8rem;
    height: 2.8rem;
}

.artist {
    font-size: 0.8rem;
    display: block;
    line-height: 1.3125rem;
    max-width: 95%;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    display: -webkit-box;
    /*white-space: nowrap;*/ overflow: hidden; text-overflow: ellipsis;
    @media(min-width: 821px) {
        font-size: 0.9375rem;
        line-clamp: 2;
    }
}

.noClick
{
    cursor:not-allowed;
}